
import React from 'react';

// eslint-disable-next-line
import Collapsible from 'react-collapsible';

// eslint-disable-next-line
import {ChangeMapLink, ChangeTopicLink} from './utils';

function Layer5(props) {

    return(
    <div className="text-holder"> <h1 id="layer-4-rhetorical-strategies-and-representation">Layer 4: Rhetorical Strategies and Representation</h1> <Collapsible trigger="Introduction" overflowWhenOpen="visible"> <p>A memorandum sent to Southern state offices on October 27, 1938, provided explicit feedback on an early collection of the life histories, explaining that "while these sketches are remarkably good for field reports, a few show a tendency towards overwriting. The most effective stories are those simply toldwhere the characters speak for themselves, with small assistance from the interviewer."<sup className="tooltip" onMouseOver={() => props.handleImage(151)}><span>"Editorial Report on State Copy: North Carolina Mill Village Sketches," October 27, 1938, Folder 1097 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[1]</div></sup> This editorial directive of letting "the characters speak for themselves" constituted the ethos of life histories and set it apart from other types of documentary writing. Sociology produced numeric summaries and case studies from the researchers point of view, while literature tended to construct composite characters emanating from stereotypes, Couch argued. In contrast, life histories were positioned as stories that better captured the interviewees actual voice, and therefore were more real, authentic, and accurate. However, the question of how to create these stories was very much up for debate as writers and editors grappled over how to write a short, coherent, and engaging story of a persons life that was in their own words.</p> <p>In a little over one year, the SLHP would negotiate the final form of a life history. At the heart of this undertaking was a series of questions, including how to convey that the story told was authentic and in the interviews own words. This provoked questions such as how the writer and interviewees subjectivity should factor into the life history, leading to decisions about content, structure, and modes of representation to formalize the method. As a result, Couch, SLHP editors, and writers negotiated a set of practices and strategies that they believed produced a more authentic, legitimate, and insightful form of documentation. They came to understand themselves as creating "human documents" through "word pictures" that documented peoples stories. To create these human documents, they used a series of strategies that were understood as observational and therefore objective rather than as making arguments or judgments about society. They were to be the empirical data that could be mined and put together to reveal new aspects of American society.</p> <p>In this layer, we analyze the form of the life histories as written documents. The socially constituted systems they produced involved a dynamic network of players: writers, interviewees, editors, directors, and the Washington FWP office. Each of these contributed their own influences over the form of the life histories. By investigating the draft, edited, and final written documents produced within these systems, we identify how the SLHP sought to position the interviewees as authentic and real. This was achieved by using strategies such as beginning with descriptions of the home space to set the scene of the interview, privileging first-person and quotations, and implementing written dialect to represent stereotypical notions of blackness. The strategies positioned the writer as present and often explicitly welcomed to bear witness to the persons story. The writer acted as a scribe, documenting the persons story in their own words and style of speech. These features were designed to convey to the reader that they were hearing an interviewees actual voice, which gave the life history credibility. The SLHP hoped these features would motivate them to identify and empathize with people in their life histories.</p> <p>Our digitized collection of life histories serves as a rich data source for investigating the forms and functions of the written records produced by the SLHP.<sup className="tooltip"><span>For more about the data and text analysis, see the <span onClick={() => props.handlePage(7)}>Methods</span>.</span><div>[2]</div></sup> Computational methods are used to augment and assist in a close reading of individual life histories. In this layer, we use two text-analysis techniques to help identify patterns within the collection of life histories. Following the terminology in corpus linguistics, each life history will be referred to as a "document." Topic models are used to identify "topics"groups of words that tend to occur together within the same documents. Document clustering is used to find groups of documents that tend to use a similar collection of words. Together, these techniques allow us to organize the lexicon of words and collections of documents in semantically meaningful ways that help identify and understand how a close reading of an individual life history relates to large-scale patterns. Further details of these techniques and how they were applied to the collection are given in the Methods Section. Links to topics and clusters of interest are included throughout the layer.</p> </Collapsible> <Collapsible trigger="Understanding through Human Documents" overflowWhenOpen="visible"> <p>As discussed in <span onClick={() => props.handlePage(4)}>Layer 3</span>, the SLHP was able to carve out a unique place in the ecosystem of documentary projects in the FWP by positioning the project as concerned with capturing the life histories of "typical" Southerners from an array of occupational sectors, thereby distinguishing it from the Social-ethnic and Folklore projects. Unlike the Social-ethnic Studies, the SLHP was not interested in documenting peoples stories from what they defined as ethnic communities, but instead "typical" Americans, which came to be understood as those individuals who SLHP administrators and staff could identify as either Black or White. Moreover, the SLHP was careful to distinguish itself from the Folklore project viewing folklore as concerned with capturing fading artifacts of culture passed down orally from generation to generation, indebted to concepts in anthropology. Instead, the SLHP redefined the qualitative method of life histories from Sociology. Life histories had been used in Sociology, but primarily to document those deemed too deviant as a way for researchers to understand how such delinquency was produced.<sup className="tooltip"><span>For examples see Clifford R. Shaw and E. W. Burgess, <em>The Jack-Roller: A Delinquent Boys Own Story</em> (Chicago (Ill.): Martino Fine Books, 2013); Herbert Blumer, <em>An Appraisal of Thomas and Znanieckis The Polish Peasant in Europe and America</em> (New York, Social science research council, 1939), <a target="_blank" rel="noopener noreferrer" href="http://archive.org/details/appraisalofthoma0000blum">[link]</a>.</span><div>[3]</div></sup> Couch felt that such a focus on deviance missed life histories true potential to serve as "human documents."</p> <p>The SLHPs investment was less about proving that life histories were more or less scientific than other methods but rather a better way to understand and communicate the human condition. It was grounded in literature and documentary strategies rather than social science and written for a non-academic audience. Human-centric documents in the form of accessible, well-written stories were better positioned for understanding American communities, economic structures, and everyday life, they argued. In this way, Couch believed that life histories could reveal the "more significant aspects of the whole life experience, including memories of ancestry, written <em>from the standpoint of the individual himself</em>."<sup className="tooltip"><span>William Terry Couch, ed., <em>These Are Our Lives</em> (University of North Carolina Press, 1939), x.</span><div>[4]</div></sup> Ideally, readers would respond as Georgia FWP State Director Samuel Tupper did to Annie Roses life history of Fannie Hopkins, "You have given the story a very human quality, and after reading it, I felt that I really had seen the woman."<sup className="tooltip" onMouseOver={() => props.handleImage(152)}><span>Letter to Annie Rose from Samuel Tupper, January 5, 1939, Folder 1112 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[5]</div></sup></p> <p>For the SLHP, understanding people meant visiting, talking, and listening to individuals who, through their words, created in aggregate a more holistic picture of an aspect of society such as the economy, education, and political beliefs. Numerical summaries could never get at this complexity, for they obscured and removed the kind of evidence that, to the SLHP, was more legitimate, the actual words of people. The indexical approach mirrored the truth claims of photography. Like a photographer, the writer had to frame the scene and then record the light with their pen rather than a shutter. The image they created was to be, as SLHP administrators consistently repeated, a "word picture." Despite the strong belief in the necessity of creating a "word picture," SLHP administrators did not have a clear understanding of the particular conventions involved other than the importance of demonstrating authenticity, which became the central concern behind discussions over the form of the life history.</p> </Collapsible> <Collapsible trigger="Instructions to Writers" overflowWhenOpen="visible"> <p>Because Couch and the other SLHP administrators did not have a clear understanding of the specific conventions and structure of life histories, the instructions given to the writers were both vague and contradictory. Writers were given a rough outline of topics to cover, including family, education, income, attitudes towards work and life, religion and morals, medical needs, diet, and the use of their time. The topic models indicate writers did use the outlines as a guide. Many of the topics center on particular professions, one of the dominant subjects in the questions. For example, <ChangeTopicLink value="topic_dialect" cnum="1">Topic 1</ChangeTopicLink> focuses on factories, <ChangeTopicLink value="topic_dialect" cnum="3">Topic 3</ChangeTopicLink> on mills and barber shops, <ChangeTopicLink value="topic_dialect" cnum="5">Topic 5</ChangeTopicLink> on insurance offices, <ChangeTopicLink value="topic_dialect" cnum="14">Topic 14</ChangeTopicLink> on education, and <ChangeTopicLink value="topic_dialect" cnum="15">Topic 15</ChangeTopicLink> on the law. These topics emerge because individual life histories tend to spend a significant portion of the interview discussing topics related to occupations and local industries. However, the instructions that accompanied this document stated, "it is not desired that each life history or story follow this outline in a rigid manner [the writer] may follow the whole outline or limit himself to a part of it."<sup className="tooltip"><span>Couch, <em>These Are Our Lives</em>, 417.</span><div>[6]</div></sup> As suggested by a lack of topics related to other questions, such as religion and morals, writers would also follow their interests.</p> <p>When it came to the point of view, "it is immaterial whether the stories are written in the first, second or third person."<sup className="tooltip"><span>Ibid.</span><div>[7]</div></sup> Additionally, the instructions stated to avoid generalities and the expression of judgment. Instead, the writer "must try to discover the real feelings of the person consulted and must record this feeling regardless of his own attitude toward it." Above all, the writer should strive for "accuracy, human interest, social importance, [and] literary excellence."<sup className="tooltip"><span>Couch, <em>These Are Our Lives</em>, 418.</span><div>[8]</div></sup> These instructions left significant room for interpretation, and as a result, writers initially sent in a wide variety of stories, many of which Couch deemed inadequate. Therefore, he looked to a few writers such as Ida Moore to create models that could be emulated and began, in conversation with writers and editors, to narrow the possibilities and formalize the composition of a life history.</p> <figure><img src='img/figure4-1.png' alt='figure' onClick={() => props.handleImage(14)}/><figcaption><b>Figure 14.</b> The life history of Mary Rumbley conducted by Ida Moore. </figcaption></figure> <p>Moores life history of Mary Rumbley, a White woman and former mill worker from Burlington, North Carolina, became the primary example.<sup className="tooltip"><span>Life History of Mary Rumbley, written by Ida Moore, October 31, 1938, Folder 674 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. <a target="_blank" rel="noopener noreferrer" href="https://dc.lib.unc.edu/cdm/singleitem/collection/03709/id/593/rec/1">[link]</a></span><div>[9]</div></sup> While there are several life histories that share little in common with the conventions from Rumbleys life history, text analysis methods that read the entire SLHP collection at scale reveal common trends that most of the stories used to create the genre of the life history, including setting the scene of the encounter between the writer and interviewee followed by significant use of block quotes to center the words of the interviewee. Together, these strategies were designed to realize a "method of writing life histories from the viewpoint of the person concerned" that fulfilled the goal of "life histories as a method of revealing people," which Couch called for.<sup className="tooltip" onMouseOver={() => props.handleImage(111)}><span>"Life Histories as a Method of Revealing People," no date given, Folder 1165 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[10]</div></sup></p> </Collapsible> <Collapsible trigger="No Space Like Home" overflowWhenOpen="visible"> <p>How to open a life history was an immediate challenge. While some of the life histories opened with the interviewees words, others began with a description of the writer and how they came to share space with the interviewee. Given that writers were documenting another persons life history, not themselves, one might think that including the writer in the story would have been frowned upon. After all, these were to be word pictures from the interviewees words. However, editors believed that by indicating that the writer and interviewee(s) were occupying the same space in which the interviewer was a mere recorder of information, the life histories could better solidify their claims to accuracy and authenticity and, therefore, as a way of knowing.<sup className="tooltip"><span>Stewart, Kathleen, <em>A Space on the Side of the Road,</em> (Princeton University Press, 1996); Butler, Judith <em>Giving and Account of One Self</em> (Fordham University Press, 2005); Coles, Robert <em>Doing Documentary Work</em> (New York Public Library, 1998); Tausig, Robert <em>I Swear I Saw This: Drawings in Fieldwork Notebooks, Namely My Own</em> (University of Chicago Press, 2011); Hecht, Tobias, <em>After Life: An Ethnographic Novel</em> (Duke University Press, 2006).</span><div>[11]</div></sup></p> <p>The writers indicated their presence while simultaneously cueing the reader into the interviewees lived experience by setting the scene of the interview. As Assistant Regional Director Walter Cutter wrote to Bernice Harris:</p> <blockquote> <p>We are trying to portray the lives of real people for other real people to read and consider. To do this, the person concerned should speak as much as possible, and the description of places, i.e., grounds, houses, rooms, and furnishing, should usually be restricted to that amount which will be sufficient to set the scene and be absorbed naturally into the plan of the story. When there are two or three pages of description, before a single voice is heard, something of the vitality of the story as a human experience is lost.<sup className="tooltip" onMouseOver={() => props.handleImage(154)}><span>Letter to Bernice Harris from Walter Cutter, December 21, 1938, Folder 1108 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[12]</div></sup></p> </blockquote> <p>Accordingly, writers began to dedicate the first few paragraphs to describe how they came to be in the interviewees presence. It was not uncommon for a life history to start with a writer walking up to a home, greeting an interviewee on a porch who then invites them inside, or finds the person and walks through the porch to the interior. Once inside, the writers described the conditions often in the form of an inventory of rooms and their objects. They list features on the exterior such as the porch and gardens and interior such as items in the homes living room and kitchen. Indicating welcomed access, proximity, and intimacy established that the writer was a reliable narrator and observer. Subsequently, once the initial scene of the interview was set in the introduction, the writers would quickly get out of the way of the interview, falling into the background to allow the interviewees voice to dominate the remainder of the document. For example, this approach came to be seen in Moores life history of Rumbley. Moore recounts her initial encounter with Rumbley, describes the home space, and notes the physical appearance of Rumbley within the first two pages as a way to set the scene. She then only occasionally asks a question to maintain narrative clarity, allowing Rumbleys story to take center stage.</p> <p>In this way, Moore and her fellow writers drew on the cultural and social values and beliefs of certain spaces in early 20th-century culture, especially the domestic sphere, to make claims to intimacy while making social and economic class signals. By the 1930s, the home was understood as a private space where an invitation was required to enter. It was also a feminized space associated with female labor as well as intimacy.<sup className="tooltip"><span>Carroll Smith-Rosenberg, <em>Disorderly Conduct: Visions of Gender in Victorian America</em>, Reprint edition (New York, NY: Oxford University Press, 1987).</span><div>[13]</div></sup> Attuned to the homes cultural and social connotations, writers were encouraged to conduct interviews inside peoples houses. The ability to enter the homea place associated with the personal and privateindicated that the writer was getting one step closer to the persons interior world. Moreover, describing the home was a way to signal race and class to an imagined audience understood as possessing White middle-class sensibilities, a point that will be discussed in more detail below.</p> <p>Conducting interviews in peoples homes also offered the possibility of putting interviewees at ease so that they would be more comfortable to give genuine answers. A primary way of organizing who to interview was by occupation. Asking a mill worker questions about their job in front of their manager was a recipe for disaster. As Virginia Writers Project director Eudora Richardson wrote to writer Mary S. Venable, "Under no circumstances should you call on people in the place of employment or approach the officials of an industry. In every case, you should reach the men and women in their homes."<sup className="tooltip" onMouseOver={() => props.handleImage(113)}><span>Letter to Mary S. Venable from Eudora Richardson, November 2, 1938, Folder 1098 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[14]</div></sup> Together with these cultural and practical reasons, conducting the interviews in peoples homes demonstrated authenticity, access, and intimacy through admittance to the interior space of the home.</p> <p>Given womens claims (or relegation) to the domestic sphere and therefore to access this space, a writer in a womens body became an asset rather than a hindrance. As demonstrated in <span onClick={() => props.handlePage(4)}>Layer 3</span>, White women writers were not only hired but produced a significant amount of life histories, often interviewing people from the communities they knew well. It was not only ideas about intimacy, access, and space that shaped their hiring but a trait that they believed lent truthfulness to the life history. Sharing space, particularly domestic space, bolstered claims that the writer could access a more authentic and informative life history. White women writers, primarily from the middle class, enjoyed access to their job in the SLHP because of the gendered and racialized assumptions that undergirded the life history method.</p> </Collapsible> <Collapsible trigger="Point of View and Proximity" overflowWhenOpen="visible"> <p>While setting the scene was viewed as an important move to signal the interviews authenticity and cue the reader into notions of race and class, it was not initially clear how to establish the writers presence while making sure they didnt take over the whole frame. Central to establishing the writers presence was the question of whose point of view the opening scene should be from.<sup className="tooltip" onMouseOver={() => props.handleImage(156)}><span>Letter to George Andrews, Mary Northrop, Sidney Jones, Harriet Corley and W.O. Saunders from Edwin Bjorkman, November 9, 1938, Folder 1100 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[15]</div></sup> As writers and editors worked together to standardize the form, they settled on the use of first-person when setting the scene, as is the case with Rumbleys life history. Moore writes, "<em>I</em> went to see Mary the other morning, a brisk October morning it was, and Mary was dropping a piece of coal on the fire when <em>I</em> opened the door in response to her Come in"[emphasis ours]. This initial scene is from Moores point of view as she describes her first encounter with Rumbley, which began the interview.</p> <p>By using "I" to represent the writer at the beginning of the life history, the writer proved they were actually inhabiting the space with the person they were interviewing, in turn, lending authenticity to the story that followed. Often, they even went to great lengths to prove that they were not only allowed in the space but welcomed. Therefore, the use of first-person narrative worked to convey the intimacy between the writer and interviewee as well as the reader and the interviewee. By setting the scene and using first-person, the reader is asked to identify with the writer through the use of "I" and join them in bearing witness to the interviewees life history. These conventions relied on the spatial and affective intimacy of the home, similar to how anthropological discourse dominant at the time signaled a sense of <em>being there</em> and <em>occupying space</em> with the community being studied as a source of authority and authenticity.<sup className="tooltip"><span>Risa Applegarth, <em>Rhetoric in American Anthropology: Gender, Genre, and Science</em> (University of Pittsburgh Press, 2014), 19,</span><div>[16]</div></sup> Therefore, by setting the scene, the writer often made the reader feel like they were now in the room with the interviewee. Together, the writer and reader could listen to the interviewee recount their life history.</p> </Collapsible> <Collapsible trigger="Description without Judgment" overflowWhenOpen="visible"> <p>Because the writer was setting the scene to introduce the person recounting their life history, editors and writers also negotiated how present the writer should be in this initial prose. Writers and editors struggled over which authorial voice and perspective to privilege in the life histories. Therefore, a key part of editing the life histories was finding a balance between setting the scene and making sure the writers presence did not dominate the scene by introducing the writers feelings and judgments.</p> <p>Since the writer was supposed to be positioned as an observer simply documenting the interviewees life, their attitudes and beliefs were not supposed to emerge. A strategy became a focus on description. The lists of features, reproduced in the topic models and document clusters, reveal the approach. <ChangeTopicLink value="topic_dialect" cnum="6">Topic 6</ChangeTopicLink> serves as a particularly good example of this language, with all of its strongest associated words focusing on common areas and objects within a house: "kitchen," "yard," "living room," and "porch." The words indicate a focus on describing items at the location of the interview.</p> <p>Moore makes precisely this move-in Rumbleys life history, noting,</p> <blockquote> <p>Above her mantel, there hangs a framed family record. It is a picture containing garlands of roses, an open hook, and two centrally placed ovals bearing the words Father, Mother The room in which we sat had not been difficult to straighten. It contained an iron bed, an old Singer sewing machine, a small walnut table and the two rocking chairs before the fire. Sweeping must have been the most difficult job she had to perform because the floor was old and splintery. Many bright colored pictures, most of them calendars, were nailed to the dingy gray walls.<sup className="tooltip"><span>Life History of Mary Rumbley written by Ida Moore, October 31, 1938, Folder 674 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. <a target="_blank" rel="noopener noreferrer" href="https://dc.lib.unc.edu/cdm/singleitem/collection/03709/id/593/rec/1">[link]</a></span><div>[17]</div></sup></p> </blockquote> <p>Many others followed suit, such as writer Ina Hawkes, who described approaching Fannie Busbins farm in Georgia: "A little farther around the house I saw a large supper song vine covering the arbor and loaded with supper songs. There were many trees in the yards and the pear and pecan trees were full of fruit, but the apple and peach tree, had just about stopped bearing fruit for the season, I picked a handful of scupper-nongs and continued on around the house."<sup className="tooltip"><span>Life History of Fannie Busbin written by Ina Hawkes, September 14, 1939, Folder 163 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill.</span><div>[18]</div></sup> Setting the scene situated the writer as a keen observer and therefore able to indexically document what they saw and heard.</p> <p>Another feature of the writing that editors homed in on was words that they deemed too judgmental or opinionated. An aim was for the reader to draw their own conclusions from the interviews; reducing language that suggested an opinion or judgment, often in the form of adjectives, was a priority. The use of words such as "disreputable" or "forlorn" was frowned upon, for they were seen as introducing opinions and judgments that disrupted the writers position as an intimate but nonetheless objective observer and got in the way of the interviewees ability to tell their story on their terms. Couch was continually frustrated with the Alabama FWPs life histories and their leadership, leading him to give direct and explicit instructions about using opinions. Couch wrote a letter to Alabama State Director Myrtle Miles offering feedback on the life histories sent to him in Chapel Hill. Irritated by the inclusion of opinions through the expression of the writers feelings, he wrote,</p> <blockquote> <p>The terms disreputable and forlorn are emotive terms expressing feeling, and as used in this sentence, they express the feeling of the author. Now it happens that one of the first principles of this work is that the author is to keep his feelings out of the stories. His task is to try to get the people on paper as they see themselves, to them to their own story in their own words as much as possible, and to suppress his own feelings and attitudes.<sup className="tooltip" onMouseOver={() => props.handleImage(157)}><span>Letter to Myrtle Miles from W.T. Couch, November 12, 1938, Folder 1101 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[19]</div></sup></p> </blockquote> <p>The writer, according to Couch, was to leave their feelings and attitudes out of the story. The writer was a vehicle, like a camera, for documenting another persons life history by which the reader could then draw conclusions. To drive the point home, he continued:</p> <blockquote> <p>This kind of statement should not be made. The author should give his description and let the reader draw his conclusions as to whether the place described is a slum or not. In the next sentence I have to object to the rude shack crouched low. These terms are terribly hackneyed. The author will find that if he will talk to the people living in such a community, they will give him out of their own mouths description fresh, interesting, vivid, and far more to the point than anything he can get by crouching and sprawling.<sup className="tooltip"><span>Ibid</span><div>[20]</div></sup></p> </blockquote> <p>Looking at the text analysis also reveals the emphasis on reducing the writers attitudes when setting the scene. The clustering of primarily nouns and verbs in the theme visualizer further indicates how adjectives were less prevalent. Consider, for example, <ChangeTopicLink value="topic_dialect" cnum="14">Topic 14</ChangeTopicLink>, which focuses on teaching and education. The most prominent words in the topic include nouns describing the people and places involved in the education domain: "teacher," "college," "service," and "book." The other most strongly associated words with the topic include active verbs such as "teach," "become," "study," and "attend." None of the top 20 words associated with the topic are adjectives or adverbs. Similarly, <ChangeTopicLink value="topic_dialect" cnum="10">Topic 10</ChangeTopicLink> centers on farming. It is most associated with nouns such as "acre," "crop," "tobacco," "mule," and "horse"; as with the education topic, affective adjectives and adverbs describing farm-life are not prominent in the topic. Similar patterns appear across the other topics identified by both topic models.</p> <p>Editors also sought to reduce literary flourishes that they understood as challenging the observational stance of the writer. Citing Jack Kytles life history of Bob Curtis, Couch wrote, "The general introduction on page 1 gives information that is needed, and it conveys this information very well, but for our purpose such passages as this would be better if the information were given without the use of figures of speech. We wish to avoid the appearance of attempting to be literary."<sup className="tooltip" onMouseOver={() => props.handleImage(158)}><span>Letter to Myrtle Miles from W.T. Couch, November 31, 1938, Folder 1101 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[21]</div></sup> Using literary techniques risked centering the writers voice and authority by establishing their writing style in the document. By instead focusing on lists of features on the exterior and material culture on the interior, the life histories drew focus on the interviewee. Writers could then convey "authentic" messages about class and lifestyle, from which the reader could draw a picture in their mind of the persons living conditions and thereby develop their own interpretations.</p> <figure><img src='img/figure4-2.png' alt='figure' onClick={() => props.handleImage(15)}/><figcaption><b>Figure 15.</b> Life History of Bob Curtis by Jack Kytle. </figcaption></figure> <p>Along with attention to language, overuse of language also risked undermining the writers position as simply an observer reporting the facts who enabled the interviewee to speak for themselves. Often editors cut down the opening section. Editors constantly charged writers with "overwriting." They worried that the writers were either too focused on themselves, thereby shifting the authorial voice or overly interpreting the interviewees thoughts and feelings. For example, as mentioned earlier, an editorial report on life histories from a North Carolina Mill Village stated, "While these sketches are remarkably good for field reports, a few show a tendency towards overwriting. The most effective stories are those simply told."<sup className="tooltip" onMouseOver={() => props.handleImage(151)}><span>"Editorial Report on State Copy: North Carolina Mill Village Sketches," October 27, 1938, Folder 1097 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[22]</div></sup> The report went on to add that "the other sketches, where the research worker is neither described nor introduced, are better."<sup className="tooltip"><span>Ibid.</span><div>[23]</div></sup> A constant theme became that the writing should focus on creating word pictures to effectively convey that the reader was hearing the authentic voice of the interviewee.</p> <p>While the form of the life histories tried to assert claims of neutral observation, racialized logic still permeated. Writers and editors editorial decisions were not evenly applied. One area where racialized decision-making becomes pronounced is the set of interviews with people working in agriculture or related service sectors, particularly how they used adjectives when setting the scene and describing the home. Document <ChangeTopicLink value="cluster_nodialect" cnum="5">Cluster 5</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="6">Cluster 6</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="7">Cluster 7</ChangeTopicLink>, and <ChangeTopicLink value="cluster_nodialect" cnum="8">Cluster 8</ChangeTopicLink> contain life histories related to agricultural work.<sup className="tooltip"><span>Document clusters are determined hierarchically so that clusters appearing next to each other are more closely related than clusters farther away from one another. See the <span onClick={() => props.handlePage(7)}>Methods</span> for more information.</span><div>[24]</div></sup> The interviews in Document <ChangeTopicLink value="cluster_nodialect" cnum="5">Cluster 5</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="6">Cluster 6</ChangeTopicLink>, and <ChangeTopicLink value="cluster_nodialect" cnum="7">Cluster 7</ChangeTopicLink> are predominantly from White interviewees, with no more than 19% of the interviews taken from Black interviewees. The most strongly associated words for these clusters are concrete nouns describing household objects, such as "bedroom," "kitchen," "yard," "stove," and "porch."</p> <p>In contrast, Document <ChangeTopicLink value="cluster_nodialect" cnum="8">Cluster 8</ChangeTopicLink> consists of a nearly even split between White and Black interviewees (48% vs.52%). The most strongly associated words in this include words such as "dirty" and "dingy." The use of disparaging adjectives used to describe White and Black homes interior extends to the exterior, but primarily with Black interviewees. Writers regularly set the stage by describing the homes as "dilapidated,"<sup className="tooltip"><span>For example, see: "A Day with Lula Wright," Folder 59 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill; "Carrie Dykes Midwife," Folder 73 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill; "Fannie Icord (Colored)," Folder 343 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill; "Georgia Negro," Folder 627 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill.</span><div>[25]</div></sup> and with "rickety" steps or pillars.<sup className="tooltip"><span>For example see: "At Father Bakers Home," Folder 61 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill; "Carrie Dykes Midwife," Folder 73 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill; "No Lawd, I Ant Ready," Folder 74 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill.</span><div>[26]</div></sup> Immediately situating the interviewee as residing in poverty. The descriptions framed the interviewee as unable to maintain their home, which risked playing into problematic racialized stereotypes. As a result, editorial decisions when setting the scene were applied through racialized and classed gazes.</p> </Collapsible> <Collapsible trigger="Shifting Authorial Voice with Block Quotes" overflowWhenOpen="visible"> <p>Following the setting of the scene, the reader joins the writer to listen to the life history. To suggest that the words are exactly those of the interviewee, they are in the form of a series of block quotes. Often, the block quotes are uninterrupted. Because the writer does not disrupt the interviewee, the story seemingly flows from the interviewee as a whole, from start to finish. As a result, the reader feels like they are listening to unmediated and complete life history.</p> <p>Using first-person narratives in the life histories furthered claims to documenting an authentic story. While Edwin Bjorkman wrote to fellow FWP administrator George Andrews along with writers Mary Northrop, Sidney Jones, Harriet Corley, and W.O. Saunders that accurately documenting the life history did "not mean that the stories necessarily must be told in the first person," it was often the case.<sup className="tooltip" onMouseOver={() => props.handleImage(156)}><span>Letter to George Andrews, Mary Northrop, Sidney Jones, Harriet Corley and W.O. Saunders from Edwin Bjorkman, November 9, 1938, Folder 1100 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[27]</div></sup> By suggesting that the document exactly recounts the interviewees words in first person block quotes, the life histories drew on the power of autobiography and biography.<sup className="tooltip" onMouseOver={() => props.handleImage(111)}><span>"Life Histories as a Method of Revealing People," no date given, Folder 1165 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. For more recent scholarship on the relationship between biography and life histories, see Jo Goodey, "Biographical Lessons for Criminology," <em>Theoretical Criminology</em> 4, no. 4 (2000): 47398; Liz Stanley, "On Auto/Biography in Sociology," <em>Sociology</em> 27, no. 1 (1993): 4152. </span><div>[28]</div></sup> The writer figuratively (and physically) sets the scene like a biographer to introduce the main character, the interviewee. The person inhabiting the first-person point of view then switches to the interviewee as they recount their life story in an autobiographical style. This narrative strategy seeks to eliminate the possibility that the reader is reading anything but the persons narration of their own story in their own words through quote after quote after quote.</p> <p>While block quotes denote authenticity, the reality behind how the quotes were obtained is quite murky, given that writers did not have recording equipment, instead of relying on their notes and memory. This situation created such difficulty that many writers balked at the long block quotes when reading Rumbleys story as the example they were supposed to follow. For example, after reading the life history aloud to his writers, Chalmers Murray, a district supervisor in South Carolina, explained that:</p> <blockquote> <p>Several of the workers objected to the long dialogue-or rather the long monologue - saying that it would have been utterly impossible for the author to remember page after page of conversation. I told them that they were not to take too literally the directions about giving a verbatim account of the interview. This cannot be done unless one took stenographic notes or used a dictaphone. If the person interviewed does not speak out of character in the story or is not grossly misquoted, there is little to worry about, in my opinion. Probably nothing in the way of an interview would ever be published if the verbatim recording were required.<sup className="tooltip" onMouseOver={() => props.handleImage(162)}><span>Letter to Mable Montgomery from Chalmers S. Murray, November 30, 1938, Folder 1103 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[29]</div></sup></p> </blockquote> <p>Therefore, block quotes became a strategy to convince the reader that the writer indexically documented the interviewees exact words and that the interviewee was literally speaking for themselves, despite the liberties that were taken in creating such quotations. Blockquotes were, then, a fundamental compositional element of the word pictures. They offered to give voice to people in a way that other documentary practices such as photography could not. Yet, word pictures composed of block quotes came with the challenge of how exactly to represent the voice of interviewees.</p> </Collapsible> <Collapsible trigger="Dialect as Authenticity" overflowWhenOpen="visible"> <p>Writers used dialect to bolster claims that the life history was the <em>actual</em> voice of the interviewee. Dialect is a common narrative device used to situate a person within a particular geography or positionality, such as social class, ethnicity, race, and gender. Using dialect to represent how a person spoke was designed to persuade the reader that the story was accurately recorded, a powerful technique when combined with block quotes.<sup className="tooltip"><span>Jane Raymond Walpole, "Eye Dialect in Fictional Dialogue," <em>College Composition and Communication</em> 25, no. 2 (1974): 19196.</span><div>[30]</div></sup></p> <figure><img src='img/figure4-3.png' alt='figure' onClick={() => props.handleImage(16)}/><figcaption><b>Figure 16.</b> Life history of Matt Wall by Louise Abbitt. </figcaption></figure> <p>With the use of written dialect, the SLHP entered a complicated realm. On the one hand, writing all the interviews in "standard English" could make them easier to read for one of their major audiences, the White middle- and upper-class readers who supported the literary market. On the other hand, claims to authenticity and accuracy could be bolstered, administrators argued, if the life histories read like people spoke. At a minimum, leaders like Alsberg thought dialect would make the stories a bit more dynamic and therefore engage potential readers. He wrote, "They might be useful for our racial group and folklore work in other parts of the country. I think a little more of the flavor of the local dialect will make the stories more readable, and that there should be considerably more contrast, light and dark, in the telling."<sup className="tooltip" onMouseOver={() => props.handleImage(163)}><span>Letter to W.T. Couch from Henry Alsberg, September 8, 1938, Folder 1090 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[31]</div></sup></p> <p>Dialect is a prominent feature in life histories. For example, <ChangeTopicLink value="topic_dialect" cnum="13">Topic 13</ChangeTopicLink> and <ChangeTopicLink value="topic_dialect" cnum="16">Topic 16</ChangeTopicLink> aggregate around two different geographies. <ChangeTopicLink value="topic_dialect" cnum="13">Topic 13</ChangeTopicLink> includes interviews by a plethora of writers in Alabama and South Carolina. <ChangeTopicLink value="topic_dialect" cnum="16">Topic 16</ChangeTopicLink> focuses on interviews conducted by Robert McKinney in New Orleans.<sup className="tooltip"><span>Joan Redding, "The Dillard Project: The Black Unit of the Louisiana Writers Project," <em>Louisiana History</em>, 1991, 4762.</span><div>[32]</div></sup> For each of these topics, the most prominent words are all forms of dialect. For example, "git", "jest", "reckon", "hit", "wuz", "git", "wid", and "fer" being amongst the most dominant terms.</p> <p>Assigned to the New Orleans office, McKinney was among, if not the first, Black writer hired as a part of FWP in Louisiana but was not a member of the black unit.<sup className="tooltip"><span>Redding, 49.</span><div>[33]</div></sup> The graduate of Xavier University joined an integrated unit with Hazel Breaux, Caroline Durieux, and others.<sup className="tooltip"><span>Oral history of Caroline Durieux by Margaret Fisher Dalrymple, March 31, 1975, Collection 4700.0013, T. Harry Williams Center for Oral History Collection, Louisiana State University. <a target="_blank" rel="noopener noreferrer" href="https://www.lib.lsu.edu/sites/all/files/oralhistory/abstracts/university/distinguished_faculty/Durieux_Caroline_T13.pdf">[link]</a>; Jason Berry, "Up From the Soul," <em>My New Orleans</em> (blog), April 1, 2014, <a target="_blank" rel="noopener noreferrer" href="https://www.myneworleans.com/up-from-the-soul/">[link]</a>.</span><div>[34]</div></sup> His access to Black communities in New Orleans was seen as an asset by state director Lyle Saxon.<sup className="tooltip"><span>Richard B. Megraw, "The Uneasiest State: Art, Culture, and Society in New Deal Louisiana, 1933-1943.(Volumes I and II).," 1990, 41415.</span><div>[35]</div></sup> The efforts to capture slight distinctions in ways of speaking are indicated by the slight differences in the use of dialect. For example, McKinney works to capture the dropping of "g" in the back of verbs such as "morning" and "living" and "a" in "again". The careful attention to certain kinds of linguistic features suggests the use of dialect by a local writer attuned to the nuances of local speech. To a reader from the community, the nuances of dialect could further signal, particularly to a reader from the same community, that the voice, and therefore story they are reading, is accurate and authentic. However, the challenge with using dialect was that the intended audience was not the person interviewed nor often even a member of the community from which the interviewee resided but primarily White middle class reading publics including academics and bureaucrats.</p> <p>The text analysis brings the racialized use of dialect in stark contrast. The challenge, then, is that written dialect can undermine peoples voices because of how written English functions socially and culturally. "Standard" English is unquestioned and seen as normal, whereas dialect, signaled through the spelling of words, is often linked to a series of assumptions about difference, which are often shaped by race and class. Effort to respell a word to reflect how a person pronounced a word is often interpreted as a misspelling in text and therefore associated with being uneducated or, at minimum, different from the norm.<sup className="tooltip"><span>Dennis R. Preston, "The Lil Abner Syndrome: Written Representations of Speech," <em>American Speech</em> 60, no. 4 (1985): 32836; Cited in Sylvie Dubois and Barbara M. Horvath, "Sounding Cajun: The Rhetorical Use of Dialect in Speech and Writing," <em>American Speech</em> 77, no. 3 (2002): 26487.</span><div>[36]</div></sup></p> <p>An even stronger dialect signal can be found in the document clustering model that does not remove dialect terms. The last eight Document Clusters, 25-32 (<ChangeTopicLink value="cluster_nodialect" cnum="25">Cluster 25</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="26">Cluster 26</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="27">Cluster 27</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="28">Cluster 28</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="29">Cluster 29</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="30">Cluster 30</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="31">Cluster 31</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="32">Cluster 32</ChangeTopicLink>), are all dominated by the usage of dialect. Looking at the proportion of Black interviewees in these clusters shows that the dialect was used to indicate race and class. Document Clusters <ChangeTopicLink value="cluster_nodialect" cnum="28">Cluster 28</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="29">Cluster 29</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="30">Cluster 30</ChangeTopicLink>, <ChangeTopicLink value="cluster_nodialect" cnum="31">Cluster 31</ChangeTopicLink>, and <ChangeTopicLink value="cluster_nodialect" cnum="32">Cluster 32</ChangeTopicLink> all consist of at least 57% of Black interviews, while they only made up about one-quarter of the life histories collected. Interestingly, the use of dialect does not show strong clustering by specific location or writer. The example of McKinneys work clustering further demonstrates how attention to local speech patterns was obfuscated in favor of a more general, standardized "southern" dialect, most commonly applied to Black interviewees voices. Given the racist ideologies bolstered by a culture of segregation that situated Black citizens as less than their White counterparts, dialect could also function as a strategy that furthered racist and white supremacist ideologies.</p> <p>In the life histories, dialect was not applied evenly. Dialect is so prominent in the life histories of Black interviewees that, if not removed, almost all of the Black interviews will group together based on dialect in the Topic Model. The fact that the dialect words co-locate and become the most significant "topics" of Black interviewees illuminates how computational text analysis methods can limit our analysis at best and replicate racialized and racist ways of knowing at worst. Primarily defining and exploring Black interviews by dialect risks recreating the same process as the SLHP in a computational and digital form. While the topic model offers insight into the interviews and racialized formal strategies, one risk is only computational reading with the grain and not against it. Because writers used dialect to mark race, a risk is that topic modeling and document clustering reinscribes the racialized logic of SLHP. A risk is reproducing a form of computational color blindness, a problematic racist ideology in and of itself. As a result, the models were adjusted (see Methods) to reveal subjects in the interviews beyond just dialect. To further explore, see the Themes Interface that also includes the topic models and document clusters where dialect is not removed.</p> </Collapsible> <Collapsible trigger="Obscuring the Role of the Writer" overflowWhenOpen="visible"> <p>Like the introduction, where the writer sets the scene, editors were concerned about the writers presence throughout the rest of the story as the interviewee recounted their life history through mainly block quotes. As Tennessee State Director William McDaniel wrote to Couch, "You will notice that we are not writing the life histories in any prescribed form. Usually, the type of story dictates the best manner in which to tell it. We have told this in the first person, though the third person has been used in most of the others. We are keeping the interviewer out of them all as much as possible since his presence in most cases has no constructive significance."<sup className="tooltip" onMouseOver={() => props.handleImage(164)}><span>Letter to W.T. Couch from William McDaniel, December 29, 1938, Folder 1109 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[37]</div></sup></p> <p>Disrupting the voice of the interviewee was deeply frowned upon. Constantly frustrated by the life histories coming from Alabama, Couch did not temper his criticism. Getting specific, he wrote,</p> <blockquote> <p>In this paragraph how does the author know that Nora is embarrassed by being in the same classes with childrenhow does he know Beatrice has accepted her fathers philosophy of life. She is interested only in finding a man, etc. The author should be extremely careful how he makes statements like these. If Nora and Beatrice said things which made him come to these conclusions, he should repeat in his story what Nora and Beatrice said and let the reader draw conclusions. If he drew his conclusions from statements made by Bob or Christine, he should quote them.<sup className="tooltip" onMouseOver={() => props.handleImage(158)}><span>Letter to Myrtle Miles from W.T. Couch, November 31, 1938, Folder 1101 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[38]</div></sup></p> </blockquote> <p>In other words, the writer should make sure to position such judgments as emanating from the interviewee by including them in the block quotes. They should not be in the words of the writer.</p> <p>Editorial notes across the life histories indicate a significant amount of time was spent removing the writer and forefronting the interviewee. The way that the life histories were writtenspecifically the use of block quotes and dialectwere intended to suggest that the reader was listening to the interviewees story in their exact words. They were simply telling their story with the writer as a scribe. As a result, one could read the topics in the Theme Interface they address as an indicator of the features of social life that the interviewees found important. For example, the Topic Model and Document Cluster focus on areas such as education, employment, foodways, and the law. However, another way to read these themes is as an indicator of the intentionality of the decisions made for the subjects of the life histories. Several themes map onto the questions and themes that writers were told to explore in the instructions.</p> <figure><img src='img/figure4-4.png' alt='figure' onClick={() => props.handleImage(17)}/><figcaption><b>Figure 17.</b> From These Are Our Lives, p.420-421. </figcaption></figure> <p>Yet, the life history form obscured the role of questionnaires and conversation in shaping the interviewees story as the writer omitted the specific questions that they asked the interviewee. For example, certain writers used questionnaires modeled from the instructions that explicitly asked about areas such as food and education. In contrast, certain writers pursued their own themes, such as Rose Shepard, who asked questions that allowed White interviewees in Jacksonville to obscure the horrors of chattel slavery through lost cause romanticism and celebrate settler colonialism. Many of these interviews are contained in <ChangeTopicLink value="cluster_nodialect" cnum="14">Cluster 14</ChangeTopicLink> (with dialect). Rather than represent that back and forth through dialogue or why the writers used block quotes that obfuscated the role such prompts had in shaping the story. The aim was to reify their claims that the story was original to the interviewee and not biased or shaped by the writer. By not indicating the role of conversation and questions in shaping the interviewees story through block quotes, the form obscured the writer and FWPs authorial influence at large.</p> <p>All of these strategies were in the service of producing a life history that created a word picture focused on the interviewee. Through setting the scene, the reader entered space with the interviewee. Through block quotes, the reader heard directly, and ideally without interruption, from the interviewee. Dialect made the sounds of the physical interview come to life through the written word. How to end the life history became the final challenge.</p> </Collapsible> <Collapsible trigger="Closing a Life History" overflowWhenOpen="visible"> <p>The question of how to close the life history was largely answered by the units most prolific writers. Some writers, particularly Bernice Harris, ended the life history with the words of the interviewee.<sup className="tooltip"><span>Her strong style can be seen in the fact that TOPIC2 is almost entirely dominated by Bernice Harris.</span><div>[39]</div></sup> While occasionally there was a short description to close the scene followed by a quote from the interviewee, the more common approach was to use block quotes until the end. The authorial voice remained with the interviewee, who literally had the last word.</p> <p>When writers shifted their authorial voice back to themselves by returning to their presence in the scene, editors worked to minimize or remove the writer. This often came in the form of a few sentences where the writer described leaving the location. Like a play, they were exiting the scene. The approach recentered the writer and disrupted the interviewees voice, and therefore risked undermining the work of the life history. If the pages of block quotes were meant to lull the reader into a sense that they were next to the interviewer reading the exact words, and therefore an unadulterated story of the persons life, then returning to the presence of the writer risked reminding the reader that a layer of interpretation sat between them and the interviewee. A return to the writer also risked recentering them as the main character, thereby hatching doubts about who the story was really about: was the life history a story about a writer meeting and interviewing a person or a document of the interviewees story? The SLHP editors made it clear that the goal was the latter.</p> <p>To assert the claim that the reader was listening to the person in their own words, revisers edited the life histories to let the interviewee have the last word. For example, the edits of Gertha Courics interview "A Day on the Farm" were mostly minor except for the final marks. The editor marked out the final paragraph that brought the interview back to Couric. She had written, "Soon after this Gorman came for me, thus ending a day with the two little ladies, who for fifty years, have held down a man-sized job without complaint."<sup className="tooltip"><span>Life history of Mrs.Ola Titus by Gertha Couric, "A Day on the Farm," January 20, 1939, Folder 16 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill.</span><div>[40]</div></sup> The edits appear to actually be Couch himself, who with the same penmanship wrote, "Excellent WC." Even if not Couch, the document demonstrates his approval of the interview as exemplary, which a review of notes on other life histories and his comments in his papers reveals was uncommon. However, the ending that returned to Couric needed to be cut.</p> <p>Another example further highlights the importance of the interviewees voice as the last one the reader heard. In an interview called "Life in a Shrimping and Oyster Shucking Camp," the writer Ila B. Prine ends with a three-paragraph description of the camp. The last line then reads, "Amid such surroundings, these people were very cheerful, and were delighted to have visitors."<sup className="tooltip"><span>Life history of Joe Vaughn by Ida Prine, "Life in a Shrimping and Oyster Shucking Camp," Folder 63 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill.</span><div>[41]</div></sup> Couch was anything but delighted and commented in the document that this approach was a "Bad ending."<sup className="tooltip"><span>Ibid</span><div>[42]</div></sup> As both examples demonstrate, SLHP editors and writers agreed that the interview should end with the authorial voice of the interviewee.</p> <p><em>These Are Our Lives</em>, the only book published of the life histories during the era, further asserts the final form of the life history as ending with the interviewees voice. With a few exceptions, life histories quickly moved into block quotes indicating the authorial shift to the interviewee and did not switch back to the writer. They end in the room with the interviewee to convey their authenticity through the intimacy of not only being in the room with the interviewee but hearing their voice last. The first and only book published of life histories offers insight into the form the SLHP settled on and the readers understanding of the success of centering the interviewees story.</p> </Collapsible> <Collapsible trigger="An Argument Published" overflowWhenOpen="visible"> <p>Throughout 1938 and 1939, the SLHP formalized the form of a life history. In a few paragraphs, the writer sets the scene. With the writer in the interviewees physical presence, the writer turned the authorial voice-over to the interviewee, who told their story. The shift in authorial voice was indicated by the use of first-person and block quotes. In one block quote after another, the interviewee often began with the beginning of their life and moved to the present, uninterrupted. The reader was positioned as having joined the writer to bear witness to the exact words, literally quoted, from the interviewee. The life history then ended with the interviewee getting the last word. The form was designed to make sure the life histories came from the viewpoint of the person telling their story, an important shift in perspective that allowed individuals to speak and be heard.</p> <p>Each document was a single persons life history, but they werent designed to be read in isolation. Couch argued that building, selecting, and organizing life histories into a collection was a critical way that the life histories produced knowledge. "Until after a large amount of material has been collected and studied, it is not possible to know what is most important, most typical, or how stories should be classified and published in order to give the most faithful representation," wrote Couch.<sup className="tooltip"><span>Couch, <em>These Are Our Lives</em>, xi.</span><div>[43]</div></sup> The technology of the book, therefore, became a strategy for how life histories created knowledge.</p> <p>How the life histories were organized and therefore read became another defining feature. The life histories were intended to be read in aggregate. The insights they revealed were designed to be produced through repetition, by reading one story after another to paint a "word picture" through "human documents" of society. As Couch wrote in the Preface to <em>These Are Our Lives</em> (TAOL), "The idea is to get life histories which are readable and faithful representations of living persons, and which, taken together, will give a fair picture of the structure and working of society."<sup className="tooltip"><span>Couch, ix.</span><div>[44]</div></sup></p> <p>As a result, the SLHP directly entered debates about sociological knowledge. The books form was also an implicit critique of prominent sociologists, who believed that quantitative data was at the heart of producing a fair picture of society. As a part of their efforts to fortify their claims to a science, prominent sociologists amplified calls to prioritize quantitative analysis. For example, the case method advocates argued that the qualitative data that comprised a case study, such as life histories, should be turned into quantitative data. Quantitative methods could then be used to identify and classify patterns in social behavior that could identify social types and social laws.<sup className="tooltip"><span>Katharine Jocher, "The Case Method in Social Research," <em>Social Forces</em>, 1928, 20311; George A. Lundberg, "Case Work and the Statistical Method," <em>Soc. F.</em> 5 (1926): 61; George Andrew Lundberg, "Social Research; a Study in Methods of Gathering Data," 1942; Howard Washington Odum and Katharine Jocher, "An Introduction to Social Research.," 1929; Wiley B. Sanders, "Recent Contributions in the Field of Juvenile Delinquency, Child Welfare, and Family Case Work," <em>Social Forces</em> 6, no. 4 (1928): 64853.</span><div>[45]</div></sup> These methods were seen as more objective and less biased. Much of this work was driven by sociologists invested in social work, particularly the study of deviance that led to the field of criminology. TAOL joined growing critiques that quantitative data was the way to glean insights about social conditions. Rather, the process of reading individual stories in aggregate - one after another - allowed the reader also to identify patterns. Qualitative data in the form of life histories, in other words, could shed light on social structures and society. Accordingly, the SLHP planned a series of books on topics such as mill village life and oil workers, though TAOL was the only book published. It was not that the literary marketplace didnt respond positively to the book, in fact the first print quickly sold out. Rather larger political issues and the onset of WWII disrupted these larger publishing plans.</p> <figure><img src='img/figure4-5.jpg' alt='figure' onClick={() => props.handleImage(18)}/><figcaption><b>Figure 18.</b> Photograph of These Are Our Lives displayed in Zibarts Book Store in Tennessee taken by William McDaniel. </figcaption></figure> <p>While readers waited for a second printing, reviewers were not always as enamored though they were generally convinced of the method. They were persuaded by the forms claims of being the authentic, accurate voice of the people interviewed. As a reviewer in the <em>Arkansas Democrat</em> wrote, "These Are Our Lives is a new adventure in literature...Here are true stories of whites and negroes of sharecroppers, farm laborers, landowners, mill and factory workers, persons engaged in service occupations, persons on relief. These are their own stories because they are related in their own language, a language so faithfully transcribed that as you read you feel you are listening as the subjects narrate their experiences, their successes and failures, their hopes and ambitions, their fears and sorrows."<sup className="tooltip" onMouseOver={() => props.handleImage(180)}><span>Copy of "Run of the News" in the Arkansas Democrat, May 23, 1939, Folder 1141 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[46]</div></sup> The United Kingdom-based <em>Sunday Mirror</em> Magazine Section wrote:</p> <blockquote> <p>Today, Americans are meeting Americans as never before in the history of the country. These United States have had their internal troubles, their bitter sectional differencesbut today the farmer knows his security depends on the well-being of industrial centers; the mill worker knows that his food supply depends on the success of the planter.</p> <p>One way that Americans have been able to learn who their neighbors are, how they get along is by the factual reporting of the life histories of living, average Americans.</p> <p>One of the outstanding examples of such reporting is the recent publication of the Federal Writers Project book, "These Are Our Lives," presenting the stories of Southern Americans in their own words, written from the standpoint of the individuals themselves.<sup className="tooltip" onMouseOver={() => props.handleImage(166)}><span>Copy of "These are Americans  Not Rich, but Free" in the <em>Sunday Mirror Magazine</em>, July 2, 1939, Folder 1116 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[47]</div></sup></p> </blockquote> <p>As the <em>Sunday Mirrors</em> review of TAOL demonstrates, the conventions ultimately used to shape the life histories position the content as the interviewees true words. The form, in aggregate, could then shed light on social truths about society. "The method of writing life histories from the viewpoint of the person concerned is a new device," wrote Couch. "It will depend for its final justification on whether the mass of readers is enabled to gain such insight into the lives of other people as will lead to fresh appreciation and understanding. If this purpose is realized, the validity of the method is vindicated."<sup className="tooltip" onMouseOver={() => props.handleImage(111)}><span>"Life Histories as a Method of Revealing People," no date given, Folder 1165 in the Federal Writers Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill. </span><div>[48]</div></sup> The SLHP may not have been often validated by their sociology colleagues in Chapel Hill or Chicago, but their primary audience read with appreciation and understanding.</p> </Collapsible> </div>
    )
}

export {Layer5};
